


















































































































import Vue from 'vue'
import SpacesModal from '@/components/SpacesModal.vue'
import MastheadContent from '@/components/MastheadContent.vue'

export default Vue.extend({
  name: 'CustomHeader',
  components: {
    SpacesModal,
    MastheadContent
  },
  props: {
    docData: { type: Object },
    hideSpaces: { type: Boolean }
  },
  data: () => ({
    layoutData: {
      onMobile: false,
      spacesModal: false
    },
  }),

  beforeMount () {
  },

  mounted () {
    this.layoutData.onMobile = window.innerWidth < 600
    window.addEventListener('resize', () => {
      this.layoutData.onMobile = window.innerWidth < 600
    })
    this.getBackgroundImg()
  },

  methods: {

    getBackgroundImg () {
      if (!this.layoutData.onMobile && this.$route.name === 'Home') {
        const imageUrl = '/img/masthead0.jpg'
        const bgElement = document.getElementsByClassName('pattern-background')[0]
        let preloaderImg = document.createElement('img')
        preloaderImg.src = imageUrl

        preloaderImg.addEventListener('load', () => {
          // @ts-ignore
          bgElement.style.backgroundImage = `url(${imageUrl})`
          bgElement.classList.add('show-pattern')
          preloaderImg = null
        })
      }
    },

    searchEnter (search) {
      window.location.href = '/?search=' + search
    }
  }
})
