



























































import Vue from 'vue'

export default Vue.extend({
  name: 'SearchInput',
  props: {
    feed: { type: Boolean },
    narrow: { type: Boolean },
    searching: { type: Boolean },
    initialSearch: { type: String },
    existingTags: { type: Array }
  },
  data: () => ({
    filterObj: {
      search: '',
      searchOption: 'Everything',
    },
    dropdown: {
      active: -1,
      hover: false,
    },
    layoutData: {
      searchFocused: false,
    }
  }),

  beforeMount () {
    this.filterObj.search = this.initialSearch
  },

  mounted () {
  },

  watch: {
    initialSearch: {
      handler (val) {
        this.filterObj.search = val
      }
    }
  },

  computed: {
    filteredTags: function () {
      return this.filterObj.search ?
        this.existingTags.filter(tag => tag.text.toLowerCase().includes(this.filterObj.search.toLowerCase())).slice(0, 5)
        : []
    }
  },

  methods: {
    searchEnter () {
      if (this.dropdown.active !== -1) {
        if (this.feed) {
          this.$emit('selected-tag', this.filteredTags[this.dropdown.active].text)
          this.filterObj.search = ''
          this.dropdown.active = -1
        } else {
          this.$router.push('/?tags=' + this.filteredTags[this.dropdown.active].text)
        }
      } else {
        this.$emit('search', this.filterObj.search)
      }
    },

    feedTagClick (event) {
      if (this.feed) {
        event.preventDefault()
        this.$emit('selected-tag', this.filteredTags[this.dropdown.active].text)
        this.filterObj.search = ''
        this.dropdown.active = -1
      }
    },

    arrowKeyPress (event) {
      if (event.key === 'ArrowDown') {
        event.preventDefault()
        if (this.filteredTags.length > this.dropdown.active + 1) this.dropdown.active += 1
      }
      if (event.key === 'ArrowUp') {
        event.preventDefault()
        if (this.dropdown.active > -1) this.dropdown.active -= 1
      }
    },
  }

})

