




























































































































































































































import Vue from 'vue'
import EmojiPicker from '@/components/EmojiPicker.vue'

export default Vue.extend({
  name: 'CommentSection',
  components: {
    EmojiPicker,
  },
  props: {
    docId: {type: Number},
    allowNotifications: {type: Boolean},
  },
  data: () => ({
    layoutData: {
      loading: true,
      addingComment: false,
      commentSubmitActive: false,
      commentSubmitFocusedAlready: false,
      emojiPicker: false,
      reportDialog: false,
      reportDialogCommentId: -1,
      reportDialogReason: '',
      openSnackbar: false,
      snackbarText: ' Thank you for reporting this comment, we will manually review it',
      sort: 'Best',
    },
    commentsClosed: false,
    newComment: {
      text: '',
      nickname: '',
      emailAddress: '',
      notify: true,
      replyTo: -1,
      error1: '',
      error2: ''
    },
    votedIs: [],
    showReplies: [],
    comments: [],
  }),
  computed: {
    displayedComments: function () {
      return this.comments.filter(com => (com.replyTo === -1 || com.show === true || this.showReplies.indexOf(com.replyTo) >= 0))
    }
  },

  beforeMount () {
    this.votedIds = JSON.parse(localStorage.getItem('commentsvoted') || '[]')
    this.getComments()

    this.newComment.nickname = sessionStorage.getItem('commentsNickname') || ''
    this.newComment.emailAddress = sessionStorage.getItem('commentsEmailAddress') || ''
  },

  mounted () {
    if (!this.commentSubmitFocusedAlready) {
      setTimeout(() => document.getElementById('text-input').focus({preventScroll: true}), 1000)
      this.commentSubmitFocusedAlready = true
    }
    /*window.addEventListener('scroll', () => {
      // if ((window.pageYOffset + window.innerHeight - 150) > document.getElementById('comment-section').getBoundingClientRect().y) {
      if (!this.commentSubmitFocusedAlready && document.getElementById('comment-section').getBoundingClientRect().y < 300) {
        setTimeout(() =>document.getElementById('text-input').focus({preventScroll: true}), 300)
        this.commentSubmitFocusedAlready = true
      }
    })*/
  },

  watch: {
    'layoutData.sort': {
      handler () {
        this.getComments()
      },
      deep: true
    },
    'newComment.replyTo': {
      handler () {
        this.getComments()
      },
      deep: true
    },
  },

  methods: {

    getComments () {
      fetch('/api/comments/get', {
        method: 'POST',
        body: JSON.stringify({
          id: this.$route.params.docId,
          sort: this.layoutData.sort
        })
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        } else {
          r.json().then(r => {
            this.comments = r.comments
            this.layoutData.loading = false

            const params = new URL('https://heystacks.org/' + location.search).searchParams
            if (params.get('comment-report')) {
              this.layoutData.reportDialogCommentId = params.get('comment-report')
              this.layoutData.reportDialog = true
            }
          })
        }
      })
    },

    addComment() {
      this.layoutData.addingComment = true
      if (!this.newComment.text) {
        this.newComment.error1 = 'no-text'
        this.layoutData.addingComment = false
      } else if (!this.newComment.nickname) {
        this.newComment.error2 = 'no-nickname'
        this.layoutData.addingComment = false
      } else {
        sessionStorage.setItem('commentsNickname', this.newComment.nickname)
        sessionStorage.setItem('commentsEmailAddress', this.newComment.emailAddress)
        fetch('/api/comments/add', {
          method: 'POST',
          body: JSON.stringify({
            id: this.$route.params.docId,
            text: this.newComment.text,
            nickname: this.newComment.nickname,
            emailAddress: this.newComment.emailAddress,
            replyTo: this.newComment.replyTo,
            notify: this.newComment.notify,
          })
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => alert(r))
          } else {
            r.json().then(r => {
              if (this.newComment.replyTo === -1) {
                this.comments = [r.comment].concat(this.comments)
              } else {
                this.getComments()
              }
              this.newComment.error1 = ''
              this.newComment.error2 = ''
              this.newComment.text = ''
              this.newComment.notify = false
              this.newComment.replyTo = -1
              this.layoutData.commentSubmitActive = false
              this.layoutData.addingComment = false
            })
          }
        })
      }
    },

    commentAction (type, commentId) {
      let upvoted = false
      if (this.votedIds.indexOf(commentId) >= 0) upvoted = true
      fetch('/api/comments/action', {
        method: 'POST',
        body: JSON.stringify({
          id: commentId,
          type: type,
          upvoted: upvoted,
          reportReason: this.layoutData.reportDialogReason,
        })
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        } else {
          r.json().then(r => {
            if (type === 'upvote') {
              this.comments.find(com => com.id === commentId).upvotes += (upvoted ? -1 : 1)
              upvoted ? this.$delete(this.votedIds, this.votedIds.indexOf(commentId)) : this.votedIds.push(commentId)
              localStorage.setItem('commentsvoted', JSON.stringify(this.votedIds))
            } else if (type === 'report') {
              this.layoutData.reportDialog = false
              this.layoutData.reportDialogReason = ''
              this.layoutData.openSnackbar = true
            }
          })
        }
      })
    },

    initiateReply (commentId) {
      this.newComment.replyTo = commentId
      this.$nextTick(() => {
        let commentReplyMsg = this.$refs.commentReplyMsg
        console.log(commentReplyMsg.scrollHeight)
        commentReplyMsg.scrollIntoView({behavior: 'smooth'})
        setTimeout(() => this.$refs.textInput.focus(), 800)
      })
    },

    insertEmoji (emoji) {
      const el = this.$refs['textInput'].$el.querySelector('textarea')
      let cursorPos = el.selectionEnd
      this.newComment.text = this.newComment.text.substring(0, cursorPos) + emoji + this.newComment.text.substring(cursorPos)
      cursorPos += 1
      this.$nextTick(() => {
        el.setSelectionRange(cursorPos, cursorPos)
        el.focus()
      })
      this.layoutData.emojiPicker = false
    },

    formatTimeAgo (timestamp) {
      const msPerMinute = 60 * 1000
      const msPerHour = msPerMinute * 60
      const msPerDay = msPerHour * 24
      const msPerMonth = msPerDay * 30
      const msPerYear = msPerDay * 365

      const elapsed = (new Date().getTime()) - timestamp

      if (elapsed < msPerMinute) {
        return Math.round(elapsed / 1000) + 's ago'
      } else if (elapsed < msPerHour) {
        return Math.round(elapsed / msPerMinute) + 'min ago'
      } else if (elapsed < msPerDay) {
        return Math.round(elapsed / msPerHour) + 'h ago'
      } else if (elapsed < msPerMonth) {
        return Math.round(elapsed / msPerDay) + 'd ago'
      } else if (elapsed < msPerYear) {
        return Math.round(elapsed / msPerMonth) + 'mo ago'
      } else {
        return Math.round(elapsed / msPerYear) + 'y ago'
      }
    }


  }

})

