







































































import Vue from 'vue'
import MastheadContent from '@/components/MastheadContent.vue'

export default Vue.extend({
  name: 'SpaceHeader',
  components: {
    MastheadContent
  },
  props: {
    docData: { type: Object },
    spaceName: { type: String }
  },
  data: () => ({
    layoutData: {
      onboarded: true,
      onMobile: false,
      animatingMasthead: false,
    },
  }),

  beforeMount () {
    const getSpaceData = () => {
      if (
        (this.$route.params.spaceName || this.spaceName) &&
        this.space.name !== (this.$route.params.spaceName || this.spaceName)
      ) {
        // TODO: on /doc/ page check if space already in store for a doc
        fetch('/api/spaces/get', {
          method: 'POST',
          body: JSON.stringify({
            slug: this.$route.params.spaceName || this.spaceName,
            docId: this.$route.params.docId
          })
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => {
              alert(r)
            })
          } else {
            r.json().then(r => {
              this.$store.commit('setSpace', r.spaces[0])
            })
          }
        })
      }
    }

    const ssrDiv = document.getElementById('ssr-data-tags-div') || document.getElementById('ssr-data-div')
    if (ssrDiv) {
      const ssrObj = JSON.parse(ssrDiv.getAttribute('data-json'))
      if (ssrObj && (
        (ssrObj.spaceObj && ssrObj.spaceObj.name === this.$route.params.spaceName) ||
        (ssrObj.id && ssrObj.id === parseInt(this.$route.params.docId))
      )) {
        this.$store.commit('setSpace', ssrObj.spaceObj)
      }
    } else {
      getSpaceData()
    }
  },

  computed: {
    space () {
      return this.$store.state.space || {}
    }
  },

  mounted () {
    this.layoutData.onMobile = window.innerWidth < 600
    window.addEventListener('resize', () => {
      this.layoutData.onMobile = window.innerWidth < 600
    })
  },

  methods: {
    searchEnter (search) {
      if (search) {
        this.$router.push('/?search=' + search)
      }
    }
  }
})
