import Vue from 'vue'
// import Vuetify from 'vuetify/lib'
//    "vuetify-loader": "^1.3.0"
import { Ripple } from 'vuetify/lib/directives'

import Vuetify, {
  VMenu,
  VCol,
  VBtn,
  VChip,
  VRow,
  VSpacer,
  VCard,
  VCardTitle,
  VApp,
  VSnackbar,
  VInput,
  VSelect,
  VContainer,
  VPagination,
  VCardText,
  VTextField,
  VTextarea,
  VIcon,
  VCheckbox,
  VList,
  VListGroup,
  VListItem,
  VListItemTitle,
  VCombobox,
  VCardActions,
  VDialog,
  VFooter,
  VSkeletonLoader,
  VTabs,
  VTab
} from 'vuetify/lib'

// Vue.use(Vuetify)
Vue.use(Vuetify, {
  components: {
    VMenu,
    VCol,
    VBtn,
    VChip,
    VRow,
    VSpacer,
    VCard,
    VCardTitle,
    VApp,
    VSnackbar,
    VInput,
    VSelect,
    VContainer,
    VPagination,
    VCardText,
    VTextField,
    VTextarea,
    VIcon,
    VCheckbox,
    VList,
    VListGroup,
    VListItem,
    VListItemTitle,
    VCombobox,
    VCardActions,
    VDialog,
    VFooter,
    VSkeletonLoader,
    VTabs,
    VTab
  },
  directives: {
    Ripple
  }
})

export default new Vuetify({
  theme: {
    options: {
      customProperties: true
    },
    themes: {
      light: {
        primary: '#f06139',
        secondary: '#1fa363',
        accent: '#82B1FF',
        error: '#ff8878',
        info: '#2196F3',
        success: '#4CAF50',
        warning: '#FFC107',
        // background: '#f1f1f1',
        background: '#f4f5f7',
        brandGreen: '#074246',
        brandDark1: '#31343F',
        brandDark2: '#404C60',
      }
    }
  },
  global: {
    ripples: false
  },
  icons: {
    iconfont: 'fa'
  }
})
