














































































































































































import Vue from 'vue'
import EmojiPicker from '@/components/EmojiPicker.vue'
import { VCard, VCardText } from 'vuetify/lib'

export default Vue.extend({
  name: 'AddNewModal',
  components: {
    EmojiPicker,
    VCard,
    VCardText,
  },
  props: {
    existingTags: { type: Array },
    space: { type: String }
  },
  data: () => ({
    layoutData: {
      comboboxInput: null,
      addLoading: false,
      onMobile: false,
      emojiPicker: false,
      page: 'main'
    },
    newDocumentObj: {
      url: '',
      errors: [],
      description: '',
      tagCandidate: '',
      tags: []
    }
  }),

  beforeMount () {
  },

  mounted () {
    this.layoutData.onMobile = window.innerWidth < 600
    window.addEventListener('resize', () => {
      this.layoutData.onMobile = window.innerWidth < 600
    })

    this.$nextTick(() => {
      if (!this.layoutData.onMobile && this.$refs['url-input']) this.$refs['url-input'].focus()
    })
  },

  watch: {},

  methods: {

    addDocument() {
      this.newDocumentObj.errors = []
      if (!this.newDocumentObj.url) this.newDocumentObj.errors.push('no-url')
      if (!this.newDocumentObj.description) this.newDocumentObj.errors.push('no-description')
      if (!this.newDocumentObj.tags.length) this.newDocumentObj.errors.push('no-tags')
      if (
        !this.newDocumentObj.url.match(/^https:\/\/docs\.google\..{1,6}\/spreadsheets/)
        && !this.newDocumentObj.url.match(/^https:\/\/docs\.google\..{1,6}\/document/)
        && !this.newDocumentObj.url.match(/^https:\/\/docs\.google\..{1,6}\/presentation/)
      ) this.newDocumentObj.errors.push('url-error')

      if (!this.newDocumentObj.errors.length) {
        this.layoutData.addLoading = true
        const docObjCopy = Object.assign({}, this.newDocumentObj)
        docObjCopy.existingTags = []
        if (this.$route.params.spaceName) docObjCopy.space = this.$route.params.spaceName
        else if (this.space) docObjCopy.space = this.space
        fetch('/api/documents/add', {
          method: 'POST',
          body: JSON.stringify(docObjCopy)
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => {
              this.layoutData.addLoading = false
              alert(r)
            })
          } else {
            r.json().then(r => {
              if (!r.success) {
                if (r.reason === 'already-exists') alert(' This document has already been added')
                if (r.reason === 'url-error') alert(' Url error - please check if your url is in the correct format')
                if (r.reason === 'cant-access') alert(' Unable to access this document - are you sure this is a public document?')
              } else {
                this.newDocumentObj.title = ''
                this.newDocumentObj.url = ''
                this.newDocumentObj.description = ''
                this.newDocumentObj.tags = []
                if (r.approvalNeeded) {
                  this.$emit('close-approval-needed')
                } else {
                  fetch('/api/documents/create-thumbnail', {
                    method: 'POST',
                    body: JSON.stringify({ docId: r.document.id })
                  })
                  setTimeout(() => fetch(`/api/sodoc/${r.document.id}`), 3000)
                  this.$emit('close-add-new-modal', r.document)
                }
              }
              this.layoutData.addLoading = false
            })
          }
        })
      }
    },

    updateTagsOnComma (event) {
      if (event.key === 'Tab' && this.layoutData.comboboxInput) {
        event.preventDefault()
        const enterProps = { bubbles: true, cancelable: true, key: 'Enter', code: 'Enter', keyCode: 13 }
        const ke = new KeyboardEvent('keydown', enterProps)
        event.target.dispatchEvent(ke)
        setTimeout(() => document.getElementById('tags-combobox--1').focus(), 10)
      }
      if (event.key === ',') {
        event.preventDefault()
        this.addTag()
      }
      if (event.key === 'Enter') {
        if (this.newDocumentObj.tagCandidate === 'No data available') {
          this.addTag()
        } else {
          if (this.newDocumentObj.tags.length > 7) {
            this.layoutData.snackbarText = 'Maximum number of tags reached'
            this.layoutData.openSnackbar = true
            this.newDocumentObj.tags.pop()
          }
        }
      }
      setTimeout(() => this.updateTagCandidate(), 0)
    },

    addTag () {
      if (this.newDocumentObj.tags.length > 7) {
        this.layoutData.snackbarText = 'Maximum number of tags reached'
        this.layoutData.openSnackbar = true
      } else {
        if (this.layoutData.comboboxInput && this.newDocumentObj.tags.map(tag => tag.text).indexOf(this.layoutData.comboboxInput) < 0) { this.newDocumentObj.tags.push({ text: this.layoutData.comboboxInput, value: this.layoutData.comboboxInput }) }
        this.layoutData.comboboxInput = null
        this.newDocumentObj.tagCandidate = ''
      }
    },

    updateTagCandidate () {
      this.newDocumentObj.tagCandidate = (document.querySelector('.v-autocomplete__content > .v-list > .v-list-item--highlighted') || { textContent: 'No data available' }).textContent
    },

    insertEmoji (emoji) {
      const el = this.$refs['new-modal-description'].$el.querySelector('textarea')
      let cursorPos = el.selectionEnd
      this.newDocumentObj.description = this.newDocumentObj.description.substring(0, cursorPos) + emoji + this.newDocumentObj.description.substring(cursorPos)
      cursorPos += 1
      this.$nextTick(() => {
        el.setSelectionRange(cursorPos, cursorPos)
        el.focus()
      })
      this.layoutData.emojiPicker = false
    },
  }
})

