











































































































































































































































































import Vue from 'vue'
import Meta from '@/components/Meta.vue'
import TagIcon from '@/components/TagIcon.vue'
import ReportDialog from '@/components/ReportDialog.vue'
import EmojiPicker from '@/components/EmojiPicker.vue'

export default Vue.extend({
  name: 'Card',
  components: {
    Meta,
    ReportDialog,
    EmojiPicker,
    TagIcon
  },
  props: {
    document: { type: Object },
    votedIds: { type: Array },
    existingTags: { type: Array },
    selectedTags: { type: Array },
    docDetail: { type: Boolean },
    onTouch: { type: Boolean },
    wordCloudLoaded: { type: Boolean },
    space: { type: Boolean }
  },
  data: () => ({
    documentData: {},
    tagCandidate: '',
    layoutData: {
      edit: false,
      comboboxInput: null,
      errors: [],
      onMobile: false,
      onMobile500: false,

      openTitleMenu: false,
      openTitleMenuTimer: null,

      emojiPicker: false,
      openSnackbar: false,
      openDialog: false,
      snackbarText: ' Link copied'
    }
  }),

  beforeMount () {
    this.documentData = Object.assign({}, this.document)
    this.documentData.tags = this.documentData.tags.replace(/\'/g, '').split(', ').map(tag => ({ text: tag, value: tag }))
  },

  mounted () {
    this.layoutData.onMobile = window.innerWidth < 600
    this.layoutData.onMobile500 = window.innerWidth < 500
    window.addEventListener('resize', (ev) => {
      this.layoutData.onMobile = window.innerWidth < 600
      if (
        (this.layoutData.onMobile500 && window.innerWidth > 500) ||
        (!this.layoutData.onMobile500 && window.innerWidth < 500)
      ) {
        this.layoutData.onMobile500 = window.innerWidth < 500
        this.createWordCloud()
      }
      this.layoutData.onMobile500 = window.innerWidth < 500
    })

    // @ts-ignore
    if (typeof WordCloud !== 'undefined') this.createWordCloud()
  },

  watch: {
    document: {
      handler () {
        this.documentData = Object.assign({}, this.document)
        this.documentData.tags = this.documentData.tags.replace(/\'/g, '').split(', ').map(tag => ({ text: tag, value: tag }))
      },
      deep: true
    },
    'layoutData.openTitleMenu': {
      handler (val) {
        if (val) this.menuTimer()
      },
      deep: true
    },
    wordCloudLoaded: {
      handler (val) {
        if (val) this.createWordCloud()
      }
    }
  },

  computed: {
    upvoted: function () {
      return this.votedIds.indexOf(this.document.id) >= 0
    }
  },

  methods: {
    action (type, docId, reason) {
      this.layoutData.openDialog = false
      if (type === 'Report') {
        this.layoutData.openDialog = true
      } else {
        fetch('/api/action', {
          method: 'POST',
          body: JSON.stringify({
            type: type,
            docId: docId,
            reason: reason
          })
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => alert(r))
          } else {
            r.json().then(r => {
              if (r.type === 'reload-title') {
                this.document.title = r.newTitle
                this.layoutData.snackbarText = ' Title has been reloaded'
                this.layoutData.openSnackbar = true
              } else if (r.type === 'url-not-working') {
                this.layoutData.snackbarText = ' Thank you for reporting this url, we will manually review it'
                this.layoutData.openSnackbar = true
              } else if (r.type === 'report-abuse') {
                this.layoutData.snackbarText = ' Thank you for reporting this document, we will manually review it'
                this.layoutData.openSnackbar = true
                this.abuseReason = ''
              }
            })
          }
        })
      }
    },

    createWordCloud () {
      // @ts-ignore
      WordCloud(document.getElementById('html-card-wordcloud-' + this.document.id), {
        list: this.document.tags.replace(/'/g, '').split(', ').map(tag => [
          tag,
          5,
          tag
        ]),
        gridSize: 2,
        weightFactor: 10,
        origin: [window.innerWidth < 500 ? 190 : 90, 0],
        drawOutOfBound: false,
        shrinkToFit: true,
        fontFamily: 'Raleway',
        classes: 'weight-500 zoom-08 cursor-pointer text-smooth',
        backgroundColor: 'transparent',
        shuffle: false,
        rotateRatio: 0,
        color: (word) => this.textToColor(word),
        // click: (item) => { this.$emit('toggle-tags', item[2]) }
      })
    },

    textToColor (text) {
      if (this.selectedTags.includes(text)) return '#f06139'
      let hash = 0
      for (let i = 0; i < text.length; i++) {
        hash = text.charCodeAt(i) + ((hash << 5) - hash)
      }
      const color = Math.floor(Math.abs((Math.sin(hash) * 10000) % 1 * 16777216)).toString(16)
      return '#' + Array(6 - color.length + 1).join('0') + color
      // return `hsl(${hashCode(text) % 360}, 10%, 50%)`;
    },

    updateDscrp (docId) {
      const conf = confirm(' Are you sure you want to submit those edits?')
      if (conf) {
        fetch('/api/documents/update', {
          method: 'POST',
          body: JSON.stringify({
            docId: docId,
            description: this.documentData.description,
            tags: this.documentData.tags
          })
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => alert(r))
          } else {
            r.json().then(r => {
              this.layoutData.snackbarText = ' Thank you for suggesting those edits! We will review them'
              this.layoutData.openSnackbar = true
              this.$emit('update-data', {
                docId: docId,
                description: this.documentData.description,
                tags: this.documentData.tags
              })
              this.layoutData.edit = false
            })
          }
        })
      }
    },

    updateTagsOnComma (event) {
      if (event.key === 'Tab' && this.layoutData.comboboxInput) {
        event.preventDefault()
        const enterProps = { bubbles: true, cancelable: true, key: 'Enter', code: 'Enter', keyCode: 13 }
        const ke = new KeyboardEvent('keydown', enterProps)
        event.target.dispatchEvent(ke)
        setTimeout(() => document.getElementById('tags-box-' + this.documentData.id).focus(), 10)
      }
      if (event.key === ',') {
        event.preventDefault()
        this.addTag()
      }
      if (event.key === 'Enter') {
        if (this.tagCandidate === 'No data available') {
          this.addTag()
        } else {
          if (this.documentData.tags.length > 7) {
            this.layoutData.snackbarText = 'Maximum number of tags reached'
            this.layoutData.openSnackbar = true
            this.documentData.tags.pop()
          }
        }
      }
      setTimeout(() => this.updateTagCandidate(), 0)
    },

    addTag () {
      if (this.documentData.tags.length > 7) {
        this.layoutData.snackbarText = 'Maximum number of tags reached'
        this.layoutData.openSnackbar = true
      } else {
        if (this.layoutData.comboboxInput && this.documentData.tags.map(tag => tag.text).indexOf(this.layoutData.comboboxInput) < 0) { this.documentData.tags.push({ text: this.layoutData.comboboxInput, value: this.layoutData.comboboxInput }) }
        this.layoutData.comboboxInput = null
      }
    },

    updateTagCandidate () {
      this.tagCandidate = (document.querySelector('.v-autocomplete__content > .v-list > .v-list-item--highlighted') || { textContent: 'No data available' }).textContent
    },

    openDocDetails (docId) {
      if (!this.docDetail && !this.layoutData.edit && !this.layoutData.emojiPicker) {
        // this.$refs['doc-card-' + docId].$el.classList.value += ' cursor-wait'
        this.$router.push('/doc/' + docId + '/' + this.document.slug)
      }
    },

    urlOpen (newUrl) {
      window.open(newUrl, '_blank')
    },

    menuTimer (clear) {
      if (clear) clearTimeout(this.layoutData.openTitleMenuTimer)
      else this.layoutData.openTitleMenuTimer = setTimeout(() => this.layoutData.openTitleMenu = false, 4000)
    },

    rightClick (event, docId) {
      if (!this.docDetail) {
        event.preventDefault()
        window.open('/doc/' + docId + '/' + this.document.slug, '_blank')
        window.focus()
      }
    },

    insertEmoji (emoji) {
      const el = this.$refs['description-textarea-' + this.document.id].$el.querySelector('textarea')
      let cursorPos = el.selectionEnd
      this.documentData.description = this.documentData.description.substring(0, cursorPos) + emoji + this.documentData.description.substring(cursorPos)
      cursorPos += 1
      this.$nextTick(() => {
        el.setSelectionRange(cursorPos, cursorPos)
        el.focus()
      })
      this.layoutData.emojiPicker = false
    },

    copyToClipboard: function (text) {
      const modalEl = document.getElementsByClassName('modal-dialog')[0]
      const textarea = document.createElement('textarea')
      textarea.textContent = text
      textarea.style.position = 'fixed'
      !modalEl ? document.body.appendChild(textarea) : modalEl.appendChild(textarea)
      textarea.select()
      try {
        document.execCommand('copy')
        this.layoutData.snackbarText = ' Link copied'
        this.layoutData.openSnackbar = true
      } catch (ex) {
        this.layoutData.snackbarText = 'Copy to clipboard failed'
        this.layoutData.openSnackbar = true
      } finally {
        !modalEl ? document.body.removeChild(textarea) : modalEl.removeChild(textarea)
      }
    },

    formatTimeAgo (timestamp) {
      const msPerMinute = 60 * 1000
      const msPerHour = msPerMinute * 60
      const msPerDay = msPerHour * 24
      const msPerMonth = msPerDay * 30
      const msPerYear = msPerDay * 365

      const elapsed = (new Date().getTime()) - timestamp

      if (elapsed < msPerMinute) {
        return Math.round(elapsed / 1000) + ' seconds ago'
      } else if (elapsed < msPerHour) {
        return Math.round(elapsed / msPerMinute) + ' minutes ago'
      } else if (elapsed < msPerDay) {
        return Math.round(elapsed / msPerHour) + ' hours ago'
      } else if (elapsed < msPerMonth) {
        return Math.round(elapsed / msPerDay) + ' days ago'
      } else if (elapsed < msPerYear) {
        return Math.round(elapsed / msPerMonth) + ' months ago'
      } else {
        return Math.round(elapsed / msPerYear) + ' years ago'
      }
    },
  }

})

