<template>
  <div>
    <CustomHeader></CustomHeader>

    <div class="home">
      <br>
      <br>
      <br>
      <div class="w-100 text-center">
        <v-chip
          @click="unsubscribe"
          class="ma-1 px-6 d-inline-block cursor-pointer"
          style="height: 48px; border-radius: 3px !important; margin-bottom: 200px !important;"
          large
          label
        >
          <v-icon small left>far fa-envelope</v-icon>
          Disable email notifications when people comment on your document
        </v-chip>
        <br>
        <!--<v-chip
          @click="unsubscribe"
          class="ma-1 px-6 d-inline-block cursor-pointer"
          style="height: 48px; background-color: #f0f0f0; border-radius: 3px !important;"
          label
        >
          Enable email notifications when people comment on your document
        </v-chip>-->
      </div>
    </div>

    <v-snackbar v-model="snackbar.visible" style="z-index: 20">
      {{snackbar.text}}
      <v-btn @click="snackbar.visible = false" text x-small >
        Close
      </v-btn>
    </v-snackbar>

    <CustomFooter></CustomFooter>
  </div>
</template>

<script>
import CustomFooter from '@/components/CustomFooter.vue'
import CustomHeader from '@/components/CustomHeader.vue'

export default {
  name: 'CommentsUnsubscribe',
  components: {
    CustomFooter,
    CustomHeader
  },
  data: () => ({
    snackbar: {
      visible: false,
      text: ''
    }
  }),

  beforeMount () {
    localStorage.setItem('notificationModal', 'hide')
  },

  watch: {
  },

  methods: {
    unsubscribe () {
      const params = new URL('https://heystacks.org/comments-unsubscribe' + location.search).searchParams
      fetch('/api/notifications/comments-disallow', {
        method: 'POST',
        body: JSON.stringify({
          id: params.get('id')
        })
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        } else {
          this.snackbar.text = 'Unsubscribe successful'
          this.snackbar.visible = true
          setTimeout(() => window.location.href = '/', 2000)
        }
      })
    }
  }
}
</script>
