    else if (this.title === 'politics') this.unicode = ''
    else if (this.title === 'art') this.unicode = ''
    else if (this.title === 'data') this.unicode = ''
    else if (this.title === 'remotework') this.unicode = ''
    else if (this.title === 'food') this.unicode = ''
    else if (this.title === 'films') this.unicode = ''
    else if (this.title === 'movies') this.unicode = ''
    else if (this.title === 'coronavirus') this.unicode = ''
    else if (this.title === 'games') this.unicode = ''
    else if (this.title === 'freelance') this.unicode = ''
    else if (this.title === 'teaching') this.unicode = ''
    else if (this.title === 'healthcare') this.unicode = ''
    else if (this.title === 'jobs') this.unicode = ''
    else if (this.title === 'parenting') this.unicode = ''
    else if (this.title === 'music') this.unicode = ''
    else if (this.title === 'sport') this.unicode = ''
    else if (this.title === 'journalism') this.unicode = ''
    else if (this.title === 'guide') this.unicode = ''
    else if (this.title === 'animal crossing') this.unicode = ''
    else if (this.title === 'ACNH') this.unicode = ''
    else if (this.title === 'finance') this.unicode = ''
    else if (this.title === 'startup') this.unicode = ''
    else if (this.title === 'fitness') this.unicode = ''
    else if (this.title === 'films') this.unicode = ''
    else if (this.title === 'VC') this.unicode = ''
    else if (this.title === 'climate') this.unicode = ''
    else if (this.title === 'recipe') this.unicode = ''
    else if (this.title === 'recruitment') this.unicode = ''
    else if (this.title === 'vegan') this.unicode = ''
    else if (this.title === 'aviation') this.unicode = ''
    else if (this.title === 'theatre') this.unicode = ''
    else if (this.title === 'history') this.unicode = ''
    else if (this.title === 'reading') this.unicode = ''
    else if (this.title === 'technology') this.unicode = ''
