










import Vue from 'vue'

export default Vue.extend({
  name: 'SheetTabDialog',
  props: {
    loading: {type: Boolean}
  },
  data: () => ({
    sheetTabDialogVisible: false,
    checked: false,
    bottom: false,
  }),

  beforeMount () {
    this.checked = JSON.parse(localStorage.getItem('sheet-tab-dialog') || 'false')
  },

  mounted () {
    // @ts-ignore
    this.bottom = document.getElementsByClassName('masthead')[0].offsetHeight <= 200
    if (!this.bottom) {
      window.addEventListener('scroll', () => {
        let totalPageHeight = document.body.scrollHeight;
        let scrollPoint = window.scrollY + window.innerHeight;
        if (scrollPoint >= totalPageHeight) {
          this.bottom = true
          this.checkAndShowDialog()
        }
      })
    }
  },

  watch: {
    loading: {
      handler () {
        this.checkAndShowDialog()
      },
    },
  },

  methods: {
    checkAndShowDialog() {
      if (!this.loading && !this.checked && this.bottom) {
        setTimeout(() => this.sheetTabDialogVisible = true, 2000)
        setTimeout(() => this.sheetTabDialogVisible = false, 7000)
        localStorage.setItem('sheet-tab-dialog', 'true')
        this.checked = true
      }
    }
  }

})

