











































































import Vue from 'vue'

export default Vue.extend({
  name: 'EmailNotificationSignup',
  props: {
    visibleVar: { type: String }
  },
  data: () => ({
    visible: false,
    mouseMovedDown: false,
    alreadyDisplayed: false,
    added: false,
    emailAddress: '',
    error: ''
  }),

  watch: {
    visibleVar: {
      handler () {
        localStorage.setItem('notificationModal', 'true')
        this.error = ''
        this.emailAddress = ''
        this.added = false
        this.visible = true
      }
    },
    /*visible: {
      handler (val) {
        console.log(val)
      }
    }*/
  },

  beforeMount () {
  },

  mounted () {
    if (localStorage.getItem('notificationModal') !== 'hide') {
      setTimeout(() => {
        if (!this.alreadyDisplayed && !this.added) {
          this.alreadyDisplayed = true
          this.visible = true
        }
      }, 8000)
      window.addEventListener('mousemove', (event) => {
        if (!this.mouseMovedDown && event.y > 300) {
          this.mouseMovedDown = true
        }
        if (!this.alreadyDisplayed && !this.added && this.mouseMovedDown && event.y < 150) {
          this.mouseMovedDown = false
          this.alreadyDisplayed = true
          this.visible = true
        }
      })
    }
  },

  methods: {

    signUp () {
      if (!this.emailAddress.includes('@')) {
        this.error = 'Incorrect email address format'
      } else {
        fetch('/api/notifications/sign-up', {
          method: 'POST',
          body: JSON.stringify({ emailAddress: this.emailAddress })
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => alert(r))
          } else {
            this.added = true
          }
        })
        this.close()
      }
    },

    close () {
      localStorage.setItem('notificationModal', 'hide')
      this.alreadyDisplayed = true
      this.visible = false
      this.$emit('close')
    }
  }
})

