












































import Vue from 'vue'

export default Vue.extend({
  name: 'EmojiPicker',
  props: {
  },
  data: () => ({
    loading: true,
    tabs: [
      'emotions',
      'people',
      'nature',
      'food',
      'travel',
      'activities',
      'objects',
      'symbols',
      'flags',
    ],
    search: '',
    activeTab: 'emotions',
    currentEmojis: []
  }),

  beforeMount () {
    this.getEmojis()
  },

  mounted () {
    const outsideClickListener = event => {
      if (event.target.closest('.emoji-picker') === null
       && event.target.closest('.emoji-picker-btn') === null) {
        removeClickListener()
        this.$emit('close')
      }
    }
    const removeClickListener = () => document.removeEventListener('click', outsideClickListener)
    document.addEventListener('click', outsideClickListener)
  },

  watch: {
    activeTab: {
      handler(val) {
        this.loading = true
        if (val !== 'emojis') this.search = ''
        this.getEmojis()
      }
    },
    search: {
      handler(val) {
        this.loading = true
        if (!val && this.activeTab === 'emojis') this.activeTab = 'emotions'
        else if (val && this.activeTab !== 'emojis') this.activeTab = 'emojis'
        else if (val) this.loading = false
      }
    },
  },

  methods: {
    getEmojis () {
      fetch('/emojis/' + this.activeTab + '.json')
        .then(r => r.json())
        .then(r => {
          this.currentEmojis = this.search ? [].concat.apply([], Object.keys(r).map(key => { return r[key] })) : r
          this.loading = false
        })
    },

    getTabEmoji (tab) {
      if (tab === 'emotions') return ''
      else if (tab === 'people') return ''
      else if (tab === 'nature') return ''
      else if (tab === 'food') return ''
      else if (tab === 'travel') return ''
      else if (tab === 'activities') return ''
      else if (tab === 'objects') return ''
      else if (tab === 'symbols') return ''
      else if (tab === 'flags') return ''
    },

    test () {
      console.log(44)
    }
  }

})

