













































































































import Vue from 'vue'
import EmojiPicker from '@/components/EmojiPicker.vue'

export default Vue.extend({
  name: 'SpacesModal',
  components: {
    EmojiPicker,
  },
  props: {
    extended: { type: Boolean }
  },
  data: () => ({
    spaces: [],
    layoutData: {
      page: 'list',
      comboboxInput: null,
      addLoading: false,
      onMobile: false,
      emojiPicker: false,
    },
    newSpaceObj: {
      name: '',
      errors: [],
      description: '',
      emailAddress: '',
      colors: {
        color1: '#074246',
        color2: '#073746',
        color3: '#074620',
      }
    }
  }),

  beforeMount () {
    fetch('/api/spaces/get', {
      method: 'POST',
    }).then(r => {
      if (r.status !== 200) {
        r.json().then(r => {
          alert(r)
        })
      } else {
        r.json().then(r => {
          this.spaces = r.spaces
        })
      }
    })
  },

  mounted () {
  },

  methods: {

    addSpace () {
      this.newSpaceObj.errors = []
      this.layoutData.addLoading = true
      if (!this.newSpaceObj.name) this.newSpaceObj.errors.push('no-name')
      if (!this.newSpaceObj.description) this.newSpaceObj.errors.push('no-description')
      if (!this.newSpaceObj.emailAddress) this.newSpaceObj.errors.push('no-email-address')
      if (!this.newSpaceObj.emailAddress.includes('@')) this.newSpaceObj.errors.push('no-email-address')
      if (this.newSpaceObj.name.match(/[^A-Za-z0-9-_]+/g)) this.newSpaceObj.errors.push('name-error')
      if (!this.newSpaceObj.errors.length) {
        fetch('/api/spaces/add', {
          method: 'POST',
          body: JSON.stringify(this.newSpaceObj)
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => {
              this.layoutData.addLoading = false
              alert(r)
            })
          } else {
            r.json().then(r => {
              if (!r.success) {
                this.newSpaceObj.errors.push(r.reason)
              } else {
                this.$router.push('/s/' + this.newSpaceObj.name)
              }
            })
          }
        })
      } else {
        this.layoutData.addLoading = false
      }
    },

    insertEmoji (emoji) {
      const el = this.$refs['space-description'].$el.querySelector('textarea')
      let cursorPos = el.selectionEnd
      this.newSpaceObj.description = this.newSpaceObj.description.substring(0, cursorPos) + emoji + this.newSpaceObj.description.substring(cursorPos)
      cursorPos += 1
      this.$nextTick(() => {
        el.setSelectionRange(cursorPos, cursorPos)
        el.focus()
      })
      this.layoutData.emojiPicker = false
    },
  }
})
