<template>
  <div style="height: 100%">
    <Document/>
  </div>
</template>

<script>
// @ is an alias to /src
import Document from '@/components/Document.vue'

export default {
  name: 'DocDetails',
  components: {
    Document
  },

  beforeMount () {
    document.body.className = 'doc-body'
    document.documentElement.classList.add('h-100')
    const appEl = document.querySelector('.doc-body > .v-application')
    if (appEl) appEl.style.cssText = 'height:100% !important'
  },

  beforeDestroy () {
    document.documentElement.classList.remove('h-100')
    const appEl = document.querySelector('.doc-body > .v-application')
    if (appEl) appEl.style.cssText = 'height:auto !important'
    document.body.className = ''
  }
}
</script>

<style>
  .doc-body {
    height: 100% !important;
  }
  .doc-body .v-application {
    height: 100% !important;
  }
  .doc-body .v-application--wrap {
    height: 100% !important;
    min-height: auto !important;
  }
  .doc-details {
    height: 100% !important;
  }
</style>
