


































import Vue from 'vue'

export default Vue.extend({
  name: 'DocFrame',
  props: {
    url: { type: String },
    layoutData: { type: Object },
    meta: { type: Object }
  },

  data: () => ({
    soDocHeight: 800
  }),

  computed: {
    formattedUrl () {
      if (this.url.includes('/pub')) return this.url
      let url = this.url + (this.layoutData.editMode ? 'edit' : 'preview')
      if (this.meta && this.meta.gid) url += '#gid=' + this.meta.gid
      return url
    }
  },

  beforeMount () {
    window.addEventListener('message', data => {
      if (['https://docs.heystacks.org.s3.us-east-1.amazonaws.com', 'https://docs.heystacks.org'].includes(data.origin)) {
        if (data.data.height) {
          this.soDocHeight = data.data.height + 50
        }
      }
    }, false)
  }
})

