
















































































































































































































































































































































































































import Vue from 'vue'
import Card from '@/components/Card.vue'
import AddNewModal from '@/components/AddNewModal.vue'
import CustomFooter from '@/components/CustomFooter.vue'
import CustomHeader from '@/components/CustomHeader.vue'
import SpaceHeader from '@/components/SpaceHeader.vue'
import TagIcon from '@/components/TagIcon.vue'
import SearchInput from '@/components/SearchInput.vue'

export default Vue.extend({
  name: 'Feed',
  components: {
    Card,
    AddNewModal,
    CustomFooter,
    CustomHeader,
    SpaceHeader,
    SearchInput,
    TagIcon
  },
  props: {
    space: { type: Boolean }
  },
  data: () => ({
    filterObj: {
      page: 0,
      pageCount: 1,
      search: '',
      searchOption: 'Everything',
      tagSearch: '',
      sort: 'Hot',
      type: 'all', // 'docs', 'sheets'
      tags: []
    },
    layoutData: {
      loaded: false,
      onboarded: true,
      searching: false,
      showSkeleton: true,
      comboboxInput: null,
      typingTimer: {},
      onMobile: false,
      onTouch: false,
      wordCloudLoaded: false,
      openSnackbar: false,
      snackbarText: ' Link copied'
    },
    newDocumentModal: false,
    tags: [],
    documents: [],
    votedIds: [],
    existingTags: [],
    spaceData: {
      name: '',
      slug: '',
      description: '',
      customisation: '',
    }
  }),

  beforeMount () {
    if (localStorage.getItem('onboarded') !== 'yes') {
      this.layoutData.onboarded = false
    }
    localStorage.setItem('onboarded', 'yes')
    this.votedIds = JSON.parse(localStorage.getItem('voted') || '[]')

    const ssrTagsDiv = document.getElementById('ssr-data-tags-div')
    if (ssrTagsDiv) {
      this.tags = JSON.parse(ssrTagsDiv.getAttribute('data-json')).tagsObj
    }
    const ssrDocsDiv = document.getElementById('ssr-data-docs-div')
    if (ssrDocsDiv) {
      let ssrDocsData = JSON.parse(ssrDocsDiv.getAttribute('data-json'))
      ssrDocsData = ssrDocsData.map(doc => ({ ...doc, spacesArr: JSON.parse(doc.spacesArr) }))
      this.documents = ssrDocsData
    }

    const params = new URL('https://heystacks.org/' + location.search).searchParams
    if (params.get('tags')) this.filterObj.tags = params.get('tags').split(',')
    if (params.get('search')) this.filterObj.search = params.get('search')
    if (params.get('sort')) this.filterObj.sort = params.get('sort')
    if (params.get('type')) this.filterObj.type = params.get('type')
    if (params.get('page-type') && params.get('page-type') === 'add-document') this.newDocumentModal = true
    if (params.get('error') && params.get('error') === 'document-doesnt-exist') {
      this.layoutData.snackbarText = ' Doc you requested doesn\'t exist'
      this.layoutData.openSnackbar = true
    }
    if (params.get('spacecb') && params.get('spacecb') === 'reject') {
      this.layoutData.snackbarText = ' Thank you, the doc has been rejected from your gathering!'
      this.layoutData.openSnackbar = true
    }
    if (params.get('spacecb') && params.get('spacecb') === 'approve') {
      this.layoutData.snackbarText = ' Thank you, the doc has been added to your gathering!'
      this.layoutData.openSnackbar = true
      fetch('/api/documents/create-thumbnail', {
        method: 'POST',
        body: JSON.stringify({docId: params.get('spacecbid')})
      })
    }

    if (this.$route.params.spaceName) {
      this.spaceData.name = this.$route.params.spaceName
      this.spaceData.slug = this.$route.params.spaceName
    }

    this.getDocuments()

    this.layoutData.onTouch = matchMedia('(hover: none), (pointer: coarse)').matches
  },

  mounted () {
    this.layoutData.onMobile = window.innerWidth < 600
    window.addEventListener('resize', () => {
      this.layoutData.onMobile = window.innerWidth < 600
    })
    // this.$nextTick(() => this.layoutData.windowLoaded = true)
    /* this.$nextTick(() => {
      if (!this.layoutData.onMobile && this.$refs['search-input']) this.$refs['search-input'].focus()
    }) */

    if (!document.getElementById('wordcloud2-script')) {
      const head = document.getElementsByTagName('head')[0]
      const script = document.createElement('script')
      script.type = 'text/javascript'
      script.src = '/lib/wordcloud2.js'
      script.id = 'wordcloud2-script'
      script.onload = () => this.layoutData.wordCloudLoaded = true
      head.appendChild(script)
    }

    setTimeout(() => {
      try {
        // @ts-ignore
        (adsbygoogle = window.adsbygoogle || []).push({})
        /*// @ts-ignore
        window._mNHandle.queue.push(function () {
          // @ts-ignore
          window._mNDetails.loadTag('276057068', '728x90', '276057068')
        })*/
      } catch (error) {
        console.log(error)
      }
    }, 3000)
  },

  watch: {
    'filterObj.tags': {
      handler () {
        setTimeout(() => this.filterObj.tagSearch = '', 200)
      },
      deep: true
    },
    'filterObj.page': {
      handler () {
        this.reFetchDocs()
        window.scrollTo(0, 570)
      },
      deep: true
    },
    'filterObj.sort': {
      handler () {
        if (this.filterObj.page !== 0) {
          this.filterObj.page = 0
        } else {
          if (this.layoutData.loaded) {
            this.reFetchDocs()
            this.getTags()
            this.updateUrl()
          }
        }
      },
      deep: true
    },
    'filterObj.type': {
      handler () {
        if (this.layoutData.loaded) {
          this.filterObj.page !== 0 ? this.filterObj.page = 0
            : this.reFetchDocs()
          this.updateUrl()
        }
      },
      deep: true
    },
    'filterObj.searchOption': {
      handler () {
        this.reFetchDocs()
      },
      deep: true
    },
    newDocumentModal: {
      handler () {
        this.updateUrl()
      }
    }
  },

  methods: {

    reFetchDocs () {
      if (this.layoutData.loaded) {
        this.layoutData.loaded = false
        this.layoutData.showSkeleton = true
        this.documents = []
        this.getDocuments()
        // window.scrollTo(0, 0)
      }
    },

    getDocuments () {
      this.layoutData.loaded = false
      fetch('/api/documents/get', {
        method: 'POST',
        body: JSON.stringify({
          search: this.filterObj.search,
          tags: this.filterObj.tags,
          page: this.filterObj.page,
          sort: this.filterObj.sort,
          type: this.filterObj.type,
          searchOption: this.filterObj.searchOption,
          space: this.space ? this.$route.params.spaceName : null
        })
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        } else {
          r.json().then(r => {
            this.documents = r.documents
            this.filterObj.pageCount = r.pageCount
            this.layoutData.loaded = true
            this.layoutData.showSkeleton = false
            if (r.remindingTags.length) {
              this.tags = r.remindingTags
              this.getTags(true)
            }
            this.layoutData.searching = false
            this.$nextTick(() => {
              // if (this.documents.length > 2) document.getElementById('doc-card-1').after(document.getElementById('276057068'))
            })
          })
        }
      })
      if (this.filterObj.type === 'all' && !this.filterObj.search && !this.filterObj.tags.length) this.getTags()
    },

    addedNewDoc(doc) {
      if (doc) {
        this.documents.unshift(doc)
        doc.tags.split(', ').forEach(tag => {
          tag = tag.replace(/\'/g, '')
          const tagIdx = this.tags.findIndex(t => t.title === tag)
          if (tagIdx < 0) {
            this.tags.push({count: 1, title: tag})
          } else {
            this.tags[tagIdx].count += 1
          }
        })
      }
      this.newDocumentModal = false
      this.layoutData.snackbarText = ' Thank you, the doc has been added!'
      this.layoutData.openSnackbar = true
    },

    addedApprovalNeeded() {
      this.newDocumentModal = false
      this.layoutData.snackbarText = ' Thank you, the doc will be added after approval from the space owner'
      this.layoutData.openSnackbar = true
    },

    getTags (unfilteredOnly) {
      fetch('/api/tags/get', {
        method: 'POST',
        body: JSON.stringify({
          sort: this.filterObj.sort
        })
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        } else {
          r.json().then(r => {
            if (!unfilteredOnly) this.tags = r.tags
            this.existingTags = r.tags.map(tag => ({ text: tag.title, value: tag.title }))
          })
        }
      })
    },

    upvoteDocument (docId: number) {
      const votedIdx = this.votedIds.indexOf(docId)
      let remove = false
      if (votedIdx < 0) {
        this.documents.find(doc => doc.id === docId).upvotes += 1
        this.votedIds.push(docId)
      } else {
        this.documents.find(doc => doc.id === docId).upvotes -= 1
        this.votedIds.splice(votedIdx, 1)
        remove = true
      }
      fetch('/api/documents/vote', {
        method: 'POST',
        body: JSON.stringify({ docId: docId, remove: remove })
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        }
      })
      localStorage.setItem('voted', JSON.stringify(this.votedIds))
    },

    searchEnter () {
      clearTimeout(this.layoutData.typingTimer)
      this.filterObj.page = 0
      this.layoutData.searching = true
      this.getDocuments()
      this.updateUrl()
    },

    /* search() {
      clearTimeout(this.layoutData.typingTimer)
      this.layoutData.typingTimer = setTimeout(() => {
        this.filterObj.page = 0
        this.getDocuments()
        this.updateUrl()
      }, 800)
    }, */

    updateDocData (updateObj) {
      const docIdx = this.documents.findIndex(doc => doc.id === updateObj.docId)
      this.documents[docIdx].description = updateObj.description
      this.documents[docIdx].tags = updateObj.tags.map(tag => '\'' + (tag.text ? tag.text : tag).replace(/\'/g, '"') + '\'').join(', ')
    },

    toggleTags (tag: string) {
      if (this.space) {
        this.$router.push('/?tags=' + tag)
      } else {
        this.filterObj.page = 0
        const tagIdx = this.filterObj.tags.indexOf(tag)
        if (tagIdx < 0) {
          this.filterObj.tags.push(tag)
          if (!this.documents.length) this.filterObj.search = ''
        } else {
          this.filterObj.tags.splice(tagIdx, 1)
        }
        this.reFetchDocs()
        this.updateUrl()
      }
    },

    updateUrl () {
      const params = new URLSearchParams({})
      if (!!this.filterObj.type && this.filterObj.type !== 'all') params.append('type', encodeURI(this.filterObj.type))
      if (!!this.filterObj.sort && this.filterObj.sort !== 'Hot') params.append('sort', encodeURI(this.filterObj.sort))
      if (!!(this.filterObj.tags && this.filterObj.tags.length)) params.append('tags', this.filterObj.tags.map(tag => encodeURI(tag)))
      if (!!this.filterObj.search) params.append('search', encodeURI(this.filterObj.search))
      if (!!this.newDocumentModal) params.append('page-type', 'add-document')
      let paramsStr = params.toString().replace(/%2C/g, ',').replace(/%25/g, '%')
      window.history.replaceState(
        { page: 'heystacks' },
        'heystacks',
        (this.space ? '/' + this.space : '/') + paramsStr ? '?' + paramsStr : ''
      )
    },

  }

})

