














































import Vue from 'vue'

export default Vue.extend({
  name: 'Meta',
  props: {
    docId: { type: Number },
    docUrl: { type: String },
    docUrlId: { type: String },
  },
  data: () => ({
    meta: {},
    loadingThumbnail: true,
  }),

  beforeMount () {
    fetch('/api/documents/get-meta', {
      method: 'POST',
      body: JSON.stringify({
        docId: this.docId
      })
    }).then(r => {
      if (r.status !== 200) {
        r.json().then(r => alert(r))
      } else {
        r.json().then(r => {
          this.meta = r.meta
        })
      }
    })
  },

  mounted () {
    document.getElementById('open-fast-btn').addEventListener('click', () => {
      this.updateViews()
    })
    document.getElementById('thumbnail-img').addEventListener('load', () => {
      this.loadingThumbnail = false
    })
    document.getElementById('thumbnail-img').addEventListener('error', () => {
      this.loadingThumbnail = false
    })
  },

  watch: {
    'meta.lastUpdate': {
      handler () {
        this.$nextTick(() => {
          document.getElementById('thumbnail-img').addEventListener('load', () => {
            this.loadingThumbnail = false
          })
          document.getElementById('thumbnail-img').addEventListener('error', () => {
            this.loadingThumbnail = false
          })
        })
      },
      deep: true
    }
  },

  methods: {
    formatTime (zuluTime) {
      const dateObj = new Date(zuluTime)
      return dateObj.toLocaleString(undefined, { year: 'numeric', month: 'short', day: 'numeric' }).replace(',', '') + ', ' +
        dateObj.toLocaleString(undefined, { hour: 'numeric', minute: 'numeric' }).replace(',', '')
    },

    urlOpen (newUrl) {
      this.updateViews()
      window.open(newUrl, '_blank')
    },

    updateViews() {
      fetch('/api/action', {
        method: 'POST',
        body: JSON.stringify({
          type: 'Click document',
          docId: this.docId
        })
      })
    }
  }

})

