import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Space from '../views/Space.vue'
import DocDetails from '../views/DocDetails.vue'
import NewDoc from '../views/NewDoc.vue'
import NewDocRedirect from '../components/NewDocRedirect.vue'
import HallOfFame from '../views/HallOfFame.vue'
import Teams from '../views/Teams.vue'
import Unsubscribe from '../views/Unsubscribe.vue'
import CommentsUnsubscribe from '../views/CommentsUnsubscribe.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/homepage',
    name: 'Homepage'
  },
  {
    path: '/new-doc',
    name: 'NewDoc',
    component: NewDoc
  },
  {
    path: '/doc/:docId(\\d+)(/.*)?',
    name: 'DocDetails',
    component: DocDetails
  },
  {
    path: '/s/:spaceName',
    name: 'Space',
    component: Space
  },
  {
    path: '/hall-of-fame',
    name: 'HallOfFame',
    component: HallOfFame
  },
  {
    path: '/teams',
    name: 'Teams',
    component: Teams
  },
  {
    path: '/unsubscribe',
    name: 'Unsubscribe',
    component: Unsubscribe
  },
  {
    path: '/comments-unsubscribe',
    name: 'CommentsUnsubscribe',
    component: CommentsUnsubscribe
  }
  /* {
    path: '/elements',
    name: 'Elements',
    component: Elements
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/!* webpackChunkName: "about" *!/ '../views/About.vue')
  } */
]

const router = new VueRouter({
  mode: 'history',
  routes,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})

router.beforeEach((to, from, next) => {
  if (!from.name) {
    // First landing on heystacks - use SSR
    next(vm => {})
  } else {
    if (to.path.includes('/s/')) {
      fetch('/api/spaces/get', {
        method: 'POST',
        body: JSON.stringify({
          slug: to.params.spaceName,
        })
      }).then(r => r.json())
        .then(r => {
          store.commit('setSpace', r.spaces[0])
          next(vm => {
          })
        })
    } else if (to.path.includes('/doc/')) {
      fetch('/api/spaces/get', {
        method: 'POST',
        body: JSON.stringify({
          docId: to.params.docId,
        })
      }).then(r => r.json())
        .then(r => {
          store.commit('setSpace', r.spaces[0])
          next(vm => {
          })
        })
    } else {
      next(vm => {
      })
    }
  }
})

export default router
