































































































































































































































































































































































































import Vue from 'vue'
import DocFrame from '@/components/DocFrame.vue'
import DocIcon from '@/components/DocIcon.vue'
import CommentSection from '@/components/CommentSection.vue'
import EmailNotificationSignup from '@/components/EmailNotificationSignup.vue'
import CustomHeader from '@/components/CustomHeader.vue'
import SpaceHeader from '@/components/SpaceHeader.vue'
import AddNewModal from '@/components/AddNewModal.vue'
import ReportDialog from '@/components/ReportDialog.vue'
import TipDialog from '@/components/TipDialog.vue'
import SheetTabDialog from '@/components/SheetTabDialog.vue'

export default Vue.extend({
  name: 'Document',
  components: {
    DocFrame,
    DocIcon,
    EmailNotificationSignup,
    CommentSection,
    CustomHeader,
    SpaceHeader,
    AddNewModal,
    ReportDialog,
    TipDialog,
    SheetTabDialog
  },
  data: () => ({
    layoutData: {
      loading: true,
      onMobile: false,
      frameLoading: true,
      frameHeight: 600,
      frameHeightOffset: 0,
      editMode: false,
      openDialog: false,
      infoDialog: false,

      showComments: false,

      openSnackbar: false,
      snackbarText: ' Link copied',
      tipDialog: false,
    },
    linkBack: '/',
    newDocumentModal: false,
    newDocumentObj: {
      existingTags: [],
      topTag: ''
    },
    notificationSnackbarVisibleVar: '',
    votedIds: [],
    document: {},
    similarDocs: [],
    space: null,
  }),

  computed: {
    upvoted: function () {
      return this.votedIds.indexOf(this.document.id) >= 0
    },
    spaceObj () {
      return this.$store.state.space || {}
    }
  },

  beforeMount () {
    const ssrDiv = document.getElementById('ssr-data-div')
    if (ssrDiv) {
      const docObj = JSON.parse(ssrDiv.getAttribute('data-json'))
      if (docObj.id === parseInt(this.$route.params.docId)) {
        if (docObj.id) this.document = docObj
        const spaceArrObj = JSON.parse(docObj.spacesArr || '[]')
        this.space = (spaceArrObj && spaceArrObj.length) ? spaceArrObj[0] : null
      }
      document.getElementById('ssr-div').style.display = 'none'
    }

    if (window.location.search.includes('tip-currency=')) this.layoutData.tipDialog = true
    if (window.location.href.includes('#comment-section')) {
      this.layoutData.showComments = true
      const htmlEl = document.getElementsByTagName('html')[0]
      htmlEl.style.marginRight = (window.innerWidth - htmlEl.offsetWidth) + 'px'
      htmlEl.classList.add('scroll-hidden')
    }
    if (window.location.href.includes('edit=true')) this.layoutData.editMode = true

    this.layoutData.onMobile = window.innerWidth < 600
    window.addEventListener('resize', () => {
      this.layoutData.onMobile = window.innerWidth < 600
      this.resizeFrame()
      // this.layoutData.frameHeight = Math.max(window.innerHeight - (this.layoutData.onMobile ? 100 : 280), 150)
    })

    this.votedIds = JSON.parse(localStorage.getItem('voted') || '[]')

    fetch('/api/documents/get', {
      method: 'POST',
      body: JSON.stringify({
        id: this.$route.params.docId
      })
    }).then(r => {
      if (r.status !== 200) {
        r.json().then(r => alert(r))
      } else {
        r.json().then(r => {
          this.document = r.documents[0]
          this.layoutData.loading = false
          this.tags = r.remindingTags
          this.similarDocs = r.similarDocs
          this.space = (r.documents[0].spacesArr && r.documents[0].spacesArr.length) ? r.documents[0].spacesArr[0] : null
          this.getTags()
          const params = new URL('https://heystacks.org/' + location.search).searchParams
          window.history.replaceState(
            { page: 'heystacks' },
            'heystacks',
            '/doc/' + this.document.id + '/' + this.document.slug
            + (params.get('comment-report') ? '?comment-report=' + params.get('comment-report') : '')
            + (this.layoutData.showComments ? '#comment-section' : '')
          )
        })
      }
    })

    if (document.referrer.includes('.co.uk/?')
      || document.referrer.includes('.co/?')
      || document.referrer.includes('.us/?')
      || document.referrer.includes('1337/?')
      || document.referrer.includes('8080/?')
    ) this.linkBack = document.referrer
      .replace('https://', '')
      .replace('http://', '')
      .replace('sourceful.co.uk', '')
      .replace('sourceful.co', '')
      .replace('sourceful.us', '')
      .replace('heystacks.org', '')
      .replace('localhost:1337', '')
      .replace('localhost:8080', '')

    this.layoutData.onTouch = matchMedia('(hover: none), (pointer: coarse)').matches
  },

  mounted () {
    setTimeout(() => this.layoutData.frameLoading = false, 10000)
    this.resizeFrame()
    setTimeout(() => this.action('Read document', this.$route.params.docId, ''), 60000)
    // document.getElementById('top-btn-comment').addEventListener('click', () => {alert(3)})
    setTimeout(() => {
      try {
        if (this.document && this.document.ads) {
          // @ts-ignore
          (adsbygoogle = window.adsbygoogle || []).push({})
        }
      } catch (error) {
        console.log(error)
      }
    }, 6000)
  },

  watch: {
    'document.id': {
      handler (val, oldVal) {
        if (val !== oldVal) {
          this.$nextTick(() => {
            this.resizeFrame()

            document.getElementById('doc-frame').onload = () => {
              this.layoutData.frameLoading = false
            }

            fetch('/api/documents/check-for-content-update', {
              method: 'POST',
              body: JSON.stringify({
                docId: this.document.id
              })
            })
          })
        }
      },
      deep: true
    },
  },

  methods: {

    getTags () {
      fetch('/api/tags/get', {
        method: 'POST'
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        } else {
          r.json().then(r => {
            this.newDocumentObj.existingTags = r.tags.map(tag => ({ text: tag.title, value: tag.title }))
            const docTags = this.document.tags.split(', ').map(tag => tag.replace(/\'/g, ''))
            /* let topTag = {title: '', count: 0}
            docTags.forEach(tag => {
              let countTag = r.tags.find(t => t.title === tag)
              if (countTag && (countTag.count > topTag.count)) {
                topTag = {
                  title: countTag.title,
                  count: countTag.count
                }
              }
            })
            this.newDocumentObj.topTag = topTag */
          })
        }
      })
    },

    action (type, docId, reason) {
      this.layoutData.openDialog = false
      if (type === 'Report abuse') {
        this.layoutData.openDialog = true
      } else {
        fetch('/api/action', {
          method: 'POST',
          body: JSON.stringify({
            type: type,
            docId: docId,
            reason: reason
          })
        }).then(r => {
          if (r.status !== 200) {
            r.json().then(r => alert(r))
          } else {
            r.json().then(r => {
              if (r.type === 'reload-title') {
                this.document.title = r.newTitle
                this.layoutData.snackbarText = ' Title has been reloaded'
                this.layoutData.openSnackbar = true
              } else if (r.type === 'url-not-working') {
                this.layoutData.snackbarText = ' Thank you for reporting this url, we will manually review it'
                this.layoutData.openSnackbar = true
              } else if (r.type === 'report-abuse') {
                this.layoutData.snackbarText = ' Thank you for reporting this document, we will manually review it'
                this.layoutData.openSnackbar = true
                this.abuseReason = ''
              }
            })
          }
        })
      }
    },

    upvoteDocument (docId: number) {
      const votedIdx = this.votedIds.indexOf(docId)
      let remove = false
      if (votedIdx < 0) {
        this.document.upvotes += 1
        this.votedIds.push(docId)
      } else {
        this.document.upvotes -= 1
        this.votedIds.splice(votedIdx, 1)
        remove = true
      }
      fetch('/api/documents/vote', {
        method: 'POST',
        body: JSON.stringify({ docId: docId, remove: remove })
      }).then(r => {
        if (r.status !== 200) {
          r.json().then(r => alert(r))
        }
      })
      localStorage.setItem('voted', JSON.stringify(this.votedIds))
    },

    copyToClipboard: function (text) {
      const modalEl = document.getElementsByClassName('modal-dialog')[0]
      const textarea = document.createElement('textarea')
      textarea.textContent = text
      textarea.style.position = 'fixed'
      !modalEl ? document.body.appendChild(textarea) : modalEl.appendChild(textarea)
      textarea.select()
      try {
        document.execCommand('copy')
        this.layoutData.snackbarText = ' Link copied'
        this.layoutData.openSnackbar = true
      } catch (ex) {
        this.layoutData.snackbarText = 'Copy to clipboard failed'
        this.layoutData.openSnackbar = true
      } finally {
        !modalEl ? document.body.removeChild(textarea) : modalEl.removeChild(textarea)
      }
    },

    resizeFrame() {
      // banner = 60
      // title = document.getElementById('title-wrapper').offsetHeight || 55
      // action-buttons = document.getElementById('action-buttons').offsetHeight || 58 / 48
      // action-bottom = 50
      // this.$nextTick(() => this.layoutData.frameHeight = window.innerHeight - (60 + 50 + (document.getElementById('title-wrapper') || {offsetHeight: 55}).offsetHeight + (document.getElementById('action-buttons') || {offsetHeight: this.layoutData.onMobile ? 116 : 66}).offsetHeight))
      this.$nextTick(() => this.layoutData.frameHeightOffset = 2
        // @ts-ignore
        + (document.getElementsByClassName('masthead')[0].offsetHeight > 80 ? 0 : 60)
        + (document.getElementById('title-wrapper') || {offsetHeight: this.layoutData.onMobile ? 42 : 55}).offsetHeight
        + (document.getElementById('action-buttons') || {offsetHeight: this.layoutData.onMobile ? 48 : 58}).offsetHeight)
    },

    toggleComments(event) {
      if (event) event.preventDefault()
      this.layoutData.showComments = !this.layoutData.showComments
      if (this.layoutData.showComments && !window.location.href.includes('#comment-section')) {
        window.history.replaceState({ page: 'heystacks' }, 'heystacks', window.location.href + '#comment-section')
        const htmlEl = document.getElementsByTagName('html')[0]
        htmlEl.style.marginRight = (window.innerWidth - htmlEl.offsetWidth) + 'px'
        htmlEl.classList.add('scroll-hidden')
      }
      if (!this.layoutData.showComments && window.location.href.includes('#comment-section')) {
        window.history.replaceState({ page: 'heystacks' }, 'heystacks', window.location.href.replace('#comment-section', ''))
        const htmlEl = document.getElementsByTagName('html')[0]
        htmlEl.style.marginRight = '0'
        htmlEl.classList.remove('scroll-hidden')
      }
    },

    openCollectChat() {
      let head = document.getElementsByTagName('head')[0]
      let script = document.createElement('script')
      script.type = 'text/javascript'
      const openChat = () => {
        setTimeout(() => {
          // @ts-ignore
          let collectchat = window.collectchat || {}
          collectchat.open()
        }, 1000)
      }
      script.onload = () => openChat()
      script.onerror = () => openChat()
      script.src = 'https://collectcdn.com/launcher.js'
      // @ts-ignore
      window.CollectId = '5f285f84cac8ec33b6a2bf79'
      head.appendChild(script)
    },

    addedNewDoc() {
      this.newDocumentModal = false
      this.layoutData.snackbarText = ' Thank you, the doc has been added!'
      this.layoutData.openSnackbar = true
    },

    addedApprovalNeeded() {
      this.newDocumentModal = false
      this.layoutData.snackbarText = ' Thank you, the doc will be added after approval from the gathering owner'
      this.layoutData.openSnackbar = true
    },

    updateDocData (updateObj) {
      this.document.description = updateObj.description
      this.document.tags = updateObj.tags.map(tag => '\'' + (tag.text ? tag.text : tag).replace(/\'/g, '"') + '\'').join(', ')
    },

    urlRedirect (newUrl) {
      window.location.href = newUrl
    },

    urlOpen (newUrl) {
      window.open(newUrl, '_blank')
    },
  }

})

