import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    space: null
  },
  mutations: {
    setSpace (state, spaceObj) {
      state.space = spaceObj
    },

    removeSpace (state) {
      state.space = null
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {
    getSpaceData: state => {
      return state.space
    }
  }

})
