

























































































































import Vue from 'vue'
import Card from '@/components/Card.vue'

export default Vue.extend({
  name: 'TipDialog',
  components: {
    Card,
  },
  props: {
    docId: {type: Number},
  },
  data: () => ({
    layoutData: {
      loading: true,
      openSnackbar: false,
      snackbarText: ' Address copied',
    },
    showDialog: true,
    paymentType: 0,
    amount: '5.00',
    currency: 'USD',
    note: '',
    amountError: '',
    payPalButton: {},
    btcAddress: ''
  }),

  beforeMount () {
    const params = new URL('https://heystacks.org/' + location.search).searchParams
    if (params.get('tip-currency')) this.currency = params.get('tip-currency')
    if (params.get('tip-amount')) this.amount = decodeURIComponent(params.get('tip-amount'))
    if (params.get('tip-note')) this.note = params.get('tip-note')
  },

  mounted () {
    if (!window.hasOwnProperty('paypal') || !window['paypal']) {
      let head = document.getElementsByTagName('head')[0]
      let script = document.createElement('script')
      script.setAttribute('id', 'pay-pal-script')
      script.type = 'text/javascript'
      script.onload = () => {
        this.createPayPalButton()
      }
      script.src = 'https://www.paypal.com/sdk/js?client-id=AQUbilyjn6_C2hgQreBpQIgUPVju_Jf26uNAFYs8q_KHombhjXClk32jZ_Nr-qtpuoROxzD1mZdHDtPK&currency=' + this.currency
      head.appendChild(script)
    } else {
      this.createPayPalButton()
    }
  },

  beforeDestroy () {
    if (this.payPalButton) this.payPalButton.close()
  },

  watch: {
    amount: {
      handler () {
        // @ts-ignore
        if (!this.layoutData.loading) paypal.Buttons({
          updateOrder: (data, actions) => {
            return actions.order.create({
              purchase_units: [{
                amount: {
                  value: this.amount,
                  currency: this.currency
                }
              }],
              application_context: {
                shipping_preference: 'NO_SHIPPING'
              }
            })
          }
        })
      },
    },
    showDialog: {
      handler (val) {
        if (!val) this.$emit('close')
      }
    },
    currency: {
      handler (val) {
        if (!this.layoutData.loading) {
          const params = new URLSearchParams({})
          params.append('tip-currency', encodeURI(val))
          params.append('tip-amount', encodeURI(this.amount))
          params.append('tip-note', encodeURI(this.note))
          let paramsStr = params.toString().replace(/%2C/g, ',').replace(/%25/g, '%')
          window.location.href = window.location.pathname + '?' + paramsStr
        }
      }
    },
    paymentType: {
      handler(val) {
        if (val === 1) {
          fetch('/api/tips/crypto', {
            method: 'POST',
            body: JSON.stringify({
              id: this.docId,
            })
          }).then(r => {
            if (r.status !== 200) {
              r.json().then(r => alert(r))
            } else {
              r.json().then(r => {
                this.btcAddress = r.address
              })
            }
          })
        }
      }
    },
  },

  methods: {

    createPayPalButton() {
      this.$nextTick(() => {
        // @ts-ignore
        this.payPalButton = paypal.Buttons({
          style: {
            color:   'blue',
          },
          createOrder: (data, actions) => {
            return actions.order.create({
              purchase_units: [{
                amount: {
                  value: this.amount,
                  currency: this.currency
                }
              }],
              application_context: {
                shipping_preference: 'NO_SHIPPING'
              }
            })
          },
          onApprove: (data, actions) => {
            return actions.order.capture().then((details) => {
              fetch('/api/tips/tipped', {
                method: 'POST',
                body: JSON.stringify({
                  id: this.docId,
                  note: this.note,
                  payPalData: {
                    purchase_units: details.purchase_units,
                    payer: details.payer
                  }
                })
              }).then(r => {
                if (r.status !== 200) {
                  r.json().then(r => alert(r))
                } else {
                  this.payPalButton.close()
                  this.$nextTick(() => this.$emit('close-tipped'))
                }
              })
            })
          }
        })
        this.layoutData.loading = false
        this.payPalButton.render('#paypal-button-container')
      })
    },

    verifyAmount() {
      this.amountError = (this.amount == 0 || this.amount >= 0.4) ? '' : 'The tip must be over 0.40 (due to PayPal fees)'
    },

    getCurrencyPrefix(code) {
      if (code === 'USD') return '$'
      else if (code === 'GBP') return ''
      else if (code === 'EUR') return ''
      else if (code === 'CAD') return 'CA$'
      else if (code === 'AUD') return 'AU$'
    },

    copyToClipboard (evt, link) {
      evt.preventDefault()
      evt.stopPropagation()
      // @ts-ignore
      if (window.clipboardData && window.clipboardData.setData) {
        this.layoutData.openSnackbar = true
        // @ts-ignore
        return window.clipboardData.setData('Text', link)
      } else if (document.queryCommandSupported && document.queryCommandSupported('copy')) {
        const modalEl = document.getElementsByClassName('headline')[0]
        const textarea = document.createElement('textarea')
        textarea.textContent = link
        textarea.style.position = 'fixed'
        !modalEl ? document.body.appendChild(textarea) : modalEl.appendChild(textarea)
        textarea.select()
        try {
          return document.execCommand('copy')
        } catch (ex) {
          console.warn('Copy to clipboard failed.', ex)
          return false
        } finally {
          !modalEl ? document.body.removeChild(textarea) : modalEl.removeChild(textarea)
          this.layoutData.openSnackbar = true
        }
      }
    },
  }

})

